.hero-section {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 640px;
    margin-top: 0;
    margin-bottom: 0;
    overflow: hidden;
    text-align: center;

    &__overlay {
		background-color: rgb(45 30 66 / 50%);
		display: flex;
		width: 100%;
		height: 100%;
		text-align: center;
		justify-content: space-around;
		align-items: flex-end;
		position: absolute;
    }

    .hero-image {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: rgba(0, 0, 0, 0.2);

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: 50% 51%;
            transition: transform 0.3s ease;
        }
    }

    .hero-content {
        position: relative;
        z-index: 1;
        color: #fff;
        max-width: 800px;
        padding: 20px;

        h1 {
            font-family: 'DM Serif Display', serif;
            font-size: 68px;
            font-weight: 700;
            line-height: 94px;
            color: #ffffff;
            margin: 0;
        }

        p {
            margin: 0 0 20px 0;
            font-size: 1.5rem;
            line-height: 1.6;
            text-align: center;
        }

        .hero-button {
            background: rgb(45, 30, 66);
            border-radius: 999px;
            box-sizing: border-box;
            color: #FFFFFF;
            cursor: pointer;
            font-family: Inter, Helvetica, 'Apple Color Emoji', 'Segoe UI Emoji', 'NotoColorEmoji', 'Noto Color Emoji', 'Segoe UI Symbol', 'Android Emoji', EmojiSymbols, -apple-system, system-ui, 'Segoe UI', Roboto, 'Helvetica Neue', 'Noto Sans', sans-serif;
            font-size: 16px;
            font-weight: 700;
            line-height: 24px;
            opacity: 1;
            outline: 0 solid transparent;
            padding: 20px 50px;
            user-select: none;
            width: fit-content;
            word-break: break-word;
            transition: background-color 0.3s ease;
			transition: box-shadow 0.3s ease;
            margin: 10px; /* Add some margin to space out the buttons */
        }

        .hero-button:hover {
            background-color: rgba(45, 30, 66, 0.9);
			box-shadow: #ecebee 0 10px 20px -10px;

        }
		.hero-button-wrapper {
			display: flex; /* Use flexbox to make the wrapper take the height of its children */
			justify-content: center; /* Center the buttons horizontally */
			align-items: center; /* Center the buttons vertically */
			flex-wrap: wrap; /* Ensure buttons wrap on smaller screens */
			gap: 10px; /* Space between buttons */
		}
        @media screen and (max-width: 600px) {
            .hero-button {
                display: block;
                width: 100%;
                padding: 15px; /* Adjust padding for better fit on smaller screens */
                margin: 10px 0; /* Space out the buttons vertically */
                font-size: 14px; /* Adjust font size if needed */
            }

            h1 {
                font-size: 32px; /* Adjust the heading size for mobile view */
                line-height: 48px;
            }

            p {
                font-size: 1.2rem; /* Adjust paragraph size for mobile view */
                line-height: 1.4;
            }
        }
    }
}
